import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileSqlListResponse } from "../../Model/Response/FileSqlListResponse";
import { FileSqlResponse } from "../../Model/Response/FileSqlResponse";
import { FileSqlViewModel } from '../../Model/ViewModel/FileSqlViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileSqlServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileSqlServicesProvider extends FileServicesProvider {
  private FileSqls: FileSqlListResponse;
  private FileSql: FileSqlResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileSql";
    console.log("Hello FileSqlServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileSql?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileSql/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileSqls = new FileSqlListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileSqls;
    });
  }



  public PostPutAsync(FileSqlViewModel:FileSqlViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileSql";
    else
    sub_url = "FileSql/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileSqlViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileSqlViewModel,mode).map(
      item => {
        this.FileSql = new FileSqlResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileSql;
      }
    );
  }
}



