import { FileSqlServicesProvider } from "../../providers/file-sql-services/file-sql-services";
import { GlobalComponent } from "../../components/global/global";
import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import { String } from "../../Model/String";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FileSqlViewModel } from "../../Model/ViewModel/FileSqlViewModel";
import { FilePathServicesProvider } from "../../providers/file-path-services/file-path-services";
import { FilePathViewModel } from "../../Model/ViewModel/FilePathViewModel";
import { ConnServicesProvider } from "../../providers/conn-services/conn-services";
/**
 * Generated class for the FileSqlModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-sql-add-edit-modal",
  templateUrl: "file-sql-add-edit-modal.html"
})
export class FileSqlAddEditModalPage {
  func_no: number = 11;
  func_key: string = "014|" + this.func_no;
  item: FileSqlViewModel;
  default_path: FilePathViewModel[];
  sql_file_path: FilePathViewModel;
  bk_file_path: FilePathViewModel;
  mode: string;
  CanEditBatch: boolean;
  CanFileBrowser: boolean;
  title: string;
  FileSql_type: string;
  min_exec_file_seq: number = 110000;
  max_exec_file_seq: number = this.min_exec_file_seq + 9999;
  range_exec_file_seq: string;
  change_mode:string;
  sql_help:string=`
  變數說明：

  {src_path_file}=來源路徑檔名,
  {JOB01},{JOB02},{JOB03}=作業迴圈參數,
  {LC01},{LC02},{LC03}=內部檔案迴圈參數,

  動態執行另一作業方法
  系統會執行create table #job( ssis_job_no varchar(30) not null,job01 varchar(50) null,job02 varchar(50) null,job03 varchar(50) null)，
  可以將資料寫入到#job進行呼叫作業執行。
  範例1：呼叫s1_daily_job
  insert into #job(ssis_job_no)
  values ('s1_daily_job')

  範例2：呼叫s1_daily_job 具有 F 的JOB01參數
  insert into #job(ssis_job_no,job01)
  values ('s1_daily_job','F')
  `;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private FileSqlServices: FileSqlServicesProvider,
    private FilePathServices: FilePathServicesProvider,
    public loadingCtrl: LoadingController,
    private global: GlobalComponent,
    public modalCtrl: ModalController,
    private ConnServices: ConnServicesProvider,
  ) {
    this.range_exec_file_seq = String.Format(
      "{0}-{1}",
      this.min_exec_file_seq,
      this.max_exec_file_seq
    );
    this.mode = navParams.data.mode;
    this.change_mode=navParams.data.change_mode;
    this.item = new FileSqlViewModel();
    this.item.exec_file_seq = navParams.data.item.exec_file_seq;
    this.item.exec_group = navParams.data.item.exec_group;
    this.item.file_keyword = navParams.data.item.file_keyword;
    this.item.file_min_length = navParams.data.item.file_min_length;
    this.item.file_max_length = navParams.data.item.file_max_length;
    this.item.is_active = navParams.data.item.is_active;
    this.item.file_proc_key = navParams.data.item.file_proc_key;
    this.item.exec_seq = navParams.data.item.exec_seq;
    this.item.can_rerun = navParams.data.item.can_rerun;
    this.item.parameter_column_group =
      navParams.data.item.parameter_column_group;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.item.note = navParams.data.item.note;
    this.item.file_proc = navParams.data.item.file_proc;
    this.item.replace_variable = navParams.data.item.replace_variable;
    this.item.is_fail_stop = navParams.data.item.is_fail_stop;
    this.item.sql_file_path_key = navParams.data.item.sql_file_path_key;
    this.item.bk_file_path_key = navParams.data.item.bk_file_path_key;
    this.item.sql_file_path = navParams.data.item.sql_file_path;
    this.item.bk_file_path = navParams.data.item.bk_file_path;
    this.item.remote_conn_id=navParams.data.item.remote_conn_id;
    this.item.remote_conn_string=navParams.data.item.remote_conn_string;
     if (this.change_mode==="n" ) {
      this.get_default_path();
      this.get_conn_string("localdb");
    }
    if (this.change_mode==="c" ) {
      this.get_max_exec_file_seq();
      this.get_max_exec_seq();
    }
    this.CanEditBatch = navParams.data.CanEditBatch;
    this.CanFileBrowser = navParams.data.CanFileBrowser;
    this.title = "執行SQL檔案";
  }
  openNavPage(parent_path) {
    let page;
    if(parent_path.indexOf("\\")>=0)
      page="DirectoryBrowserPage";
    if(parent_path.indexOf("/")>=0)
      page="FtpDirectoryBrowserPage";
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {

      this.navCtrl.push(page, { parent_path: parent_path,parent_page:this,is_modal:true});
    });
    this.global.dismissLoading();
  }
  SelectFileProcType() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.file_proc_key,
      code_type: "018"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.file_proc_key = select_data.code_key;
      this.item.file_proc = select_data.code_desc;
    });

    modal.present();
  }
  SelectParameterGroup() {
    let modal = this.modalCtrl.create("ParameterGroupSelectModelPage", {
      select_parameter_column_group: this.item.parameter_column_group,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.parameter_column_group = select_data.parameter_column_group;
    });

    modal.present();
  }
  SelectSqlFilePath() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.sql_file_path_key,
      code_type: "005"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.sql_file_path_key = select_data.code_key;
      this.item.sql_file_path = select_data.code_desc;
    });

    modal.present();
  }

  SelectBkFilePath() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.bk_file_path_key,
      code_type: "005"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.bk_file_path_key = select_data.code_key;
      this.item.bk_file_path = select_data.code_desc;
    });

    modal.present();
  }

  get_max_exec_file_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.FileSqlServices.GetMaxExecFileSeqAsync(
        this.func_no,
        this.item.exec_group
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_file_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  get_default_path() {
    this.global.createLoader("取得預設資料夾中...");
    this.global.loading.present().then(() => {
      this.FilePathServices.GetDefaultListsByAsync(this.func_key).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.default_path = data.Model;
            this.sql_file_path = this.default_path.filter(
              (item, FilePathViewModel) => item.file_path_type_key === "004|SQL"
            )[0];
            this.item.sql_file_path_key = this.sql_file_path.file_path_key;
            this.item.sql_file_path = this.sql_file_path.file_path;

            this.bk_file_path = this.default_path.filter(
              (item, FilePathViewModel) => item.file_path_type_key === "004|BK"
            )[0];
            this.item.bk_file_path_key = this.bk_file_path.file_path_key;
            this.item.bk_file_path = this.bk_file_path.file_path;



            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  get_max_exec_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.FileSqlServices.GetMaxExecSeqAsync(
        this.func_no,
        this.item.exec_group
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  get_conn_string(remote_conn_id: string) {
    this.global.createLoader("取得連線字串中...");
    this.global.loading.present().then(() => {
      this.ConnServices.GetConnAsync(remote_conn_id).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.remote_conn_string =
              data.Model.conn_string;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  SelectConn() {
    let modal = this.modalCtrl.create("ConnSelectModalPage", {
      select_remote_conn_id: this.item.remote_conn_id,
      conn_type_key:"065|001",
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.remote_conn_id = select_data.remote_conn_id;
      this.item.remote_conn_string=select_data.conn_string;
    });

    modal.present();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  openNavParameterColumnPage(item) {
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {
      this.navCtrl.push("ParameterColumnPage", { item: item });
    });
    this.global.dismissLoading();
  }
  Help() {

    this.global.createLoader();

    this.global.loading.present().then(() => {
       this.global.showMessage("SQL語法說明",this.sql_help);

    });

  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FileSqlModalPage");
  }
}



