export class FileSqlViewModel {
  /// <summary>
  ///  轉檔序號
  /// <summary>
  public exec_file_seq: number;
  /// <summary>
  ///  轉檔群組
  /// <summary>
  public exec_group: string;
  /// <summary>
  ///  檔案搜尋關鍵字
  /// <summary>
  public file_keyword: string;
  /// <summary>
  ///  檔案最小長度
  /// <summary>
  public file_min_length: number;
  /// <summary>
  ///  檔案最大長度
  /// <summary>
  public file_max_length: number;

  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;
  /// <summary>
  ///  取代變數值
  /// <summary>
  public replace_variable: boolean;
  /// <summary>
  ///  檔案處理方式主鍵
  /// <summary>
  public file_proc_key: string;
  /// <summary>
  ///  執行順序
  /// <summary>
  public exec_seq: number;
  /// <summary>
  ///  重複執行註記
  /// <summary>
  public can_rerun: boolean;
  /// <summary>
  ///  參數欄位群組
  /// <summary>
  public parameter_column_group: string;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
  /// <summary>
  ///  備註
  /// <summary>
  public note: string;


  /// <summary>
  ///  檔案處理方式
  /// <summary>
  public file_proc: string;


  /// <summary>
  ///  執行失敗停止
  /// </summary>
  public is_fail_stop: boolean;

  /// <summary>
  ///  Sql檔案路徑主鍵
  /// </summary>

  public sql_file_path_key: string;

  /// <summary>
  ///  備份檔案路徑主鍵
  /// </summary>
  public bk_file_path_key: string;


  /// <summary>
  ///  sql檔案路徑
  /// </summary>

  public sql_file_path: string;

  /// <summary>
  ///  備份檔案路徑
  /// </summary>
  public bk_file_path: string;

  /// <summary>
  ///  啟用註記說明
  /// <summary>
  public is_active_desc: string;
  /// <summary>
  ///  啟用註記顏色
  /// <summary>
  public is_active_color: string;
  /// <summary>
  ///  啟用顏色
  /// <summary>
  public is_active_color_right: string;
  /// <summary>
///  遠端連線編號
/// <summary>
public remote_conn_id: string;

/// <summary>
///  遠端連線字串
/// <summary>
public remote_conn_string: string;
}



